.curriculum {
  background: #f4f4f4;
  font-family: "SimSun";
  height: 100%;
  margin-top: 0.2rem;
}
.curriculum .ant-layout {
  background: #f4f4f4;
}
.curriculum .ant-tabs-bar {
  width: 100% !important;
}
.curriculum .curriculum-body {
  text-align: left;
  height: 100%;
  margin-top: 0;
}
.curriculum .curriculum-body .ant-breadcrumb {
  padding: 0.2rem;
  font-size: 0.88rem;
}
.curriculum .curriculum-body .ant-breadcrumb .ant-breadcrumb-link {
  color: #827373;
}
.curriculum .curriculum-body .curriculum_layout {
  min-height: calc(100% - 30px);
}
.curriculum .curriculum-body .curriculum_header {
  background: #fff;
  padding: 0;
  height: 100%;
}
.curriculum .curriculum-body .curriculum_sider {
  background: #fff;
  margin-right: 1rem;
}
.curriculum .curriculum-body .curriculum_sider .syllabus_header {
  font-size: 1rem;
  color: #018ce0;
  font-weight: 600;
}
.curriculum .curriculum-body .curriculum_sider .syllabus_content {
  height: 100%;
  border: 1px solid #ded5d5;
}
.curriculum .curriculum-body .curriculum_content_sort {
  height: 2.69rem;
  background: #fff;
  line-height: 2.69rem;
  padding-left: 2rem;
  margin-bottom: 1rem;
}
.curriculum .curriculum-body .curriculum_content_sort a {
  font-size: 0.88rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #333333;
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-right: 6rem;
  -ms-flex-align: center;
      align-items: center;
}
.curriculum .curriculum-body .curriculum_content_sort a img {
  margin-left: 0.3rem;
}
.curriculum .curriculum-body .curriculum_content_sort a.sort_selected {
  color: #028bff;
}
.curriculum .curriculum-body .curriculum_content_title {
  height: 2rem;
  line-height: 2rem;
  padding-left: 0.3rem;
  margin-bottom: 0.3rem;
  font-size: 0.88rem;
  font-family: MicrosoftYaHei;
  font-weight: 400;
  color: #666666;
}
.curriculum .curriculum-body .curriculum_content {
  margin-top: 0.1rem;
}
.curriculum .curriculum-body .curriculum_footer {
  padding: 0;
}
.curriculum .curriculum-body .curriculum-bottom {
  background: #fff;
  text-align: center;
  padding-bottom: 1rem;
}
